import chinaArea from '../../static/vantArea.json';
export default {
  data() {
    return {
      chinaArea,
      showSelect:false,
      areaLists:[],
      initChecked:false,
      formItem:{
        addr_id:0,
        name:'',
        tel:'',
        province:'',
        city:'',
        county:'',
        address:'',
        area_code:'',
        is_default:0,
        postal_code:''
      },
    };
  },
  onLoad:function(option){
    if(option.addr_id){
      this.getAddrDetail(option.addr_id)
    }
  },
  methods: {
    getWeChatAddr(){
      const _this = this
      uni.getSetting({
        success(res) {
          if (res.authSetting['scope.address']) {
            uni.chooseAddress({
              success(res) {
                _this.formItem.name = res.userName
                _this.formItem.tel = res.telNumber
                _this.formItem.province = res.provinceName
                _this.formItem.city = res.cityName
                _this.formItem.county = res.countyName
                _this.formItem.address = res.detailInfo
                _this.formItem.area_code = res.nationalCode
                _this.formItem.postal_code = res.postalCode
                _this.add()
              }
            })
          } else {
            if (res.authSetting['scope.address'] == false) {
              uni.openSetting({
                success(res) {
                  console.log(res.authSetting)
                }
              })
            } else {
              _this.formItem.name = res.userName
              _this.formItem.tel = res.telNumber
              _this.formItem.province = res.provinceName
              _this.formItem.city = res.cityName
              _this.formItem.county = res.countyName
              _this.formItem.address = res.detailInfo
              _this.formItem.area_code = res.nationalCode
              _this.formItem.postal_code = res.postalCode
              _this.add()
            }
          }
        }
      })
    },
    getAddrDetail(id){
      this.$http({
        aurl:'addressDetail',
        aPath:[id],
        success:res => {
          const detail = res.data.result
          this.formItem = {
            addr_id:detail.id,
            name:detail.name,
            tel:detail.tel,
            province:detail.province,
            city:detail.city,
            county:detail.county,
            address:detail.address,
            area_code:detail.area_code,
            is_default:detail.is_default,
            postal_code:detail.postal_code
          }
          this.areaLists[0]={name:detail.province}
          this.areaLists[1]={name:detail.city}
          this.areaLists[2]={name:detail.county}
        },
        fail:error => {
          console.log(error)
        }
      })
    },
    getArea(e){
      this.areaLists = e.detail.values
      this.formItem.province = this.areaLists[0].name
      this.formItem.city = this.areaLists[1].name
      this.formItem.county = this.areaLists[2].name
      this.formItem.area_code = this.areaLists[2].code
      this.showSelect = false
    },
    getSwitch(e){
      this.formItem.is_default = Number(e.detail)
    },
    saveAddress(){
      if(this.formItem.addr_id===0){
        this.add()
      }else{
        this.edit()
      }
    },
    edit(){
      this.$http({
        aurl:'addressEdit',
        data:this.formItem,
        success:res => {
          if(res.data.code===0){
            this.toBack()
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    },
    add(){
      this.$http({
        aurl:'saveAddress',
        data:this.formItem,
        success:res => {
          if(res.data.code===0){
            this.toBack()
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    }
  },
  mounted() {
  },
};
